import React, {Component, Fragment} from 'react'
import {ConfigConsumer} from 'antd/lib/config-provider'
import PropTypes from "prop-types";
import Icon from "antd/lib/icon"
import Row from "antd/lib/row"
import Button from "antd/lib/button"
import { connect } from 'react-redux'
import { createSelector } from 'reselect'
import {
  sendCmd
} from '../../actions'
import Wifi from "../icons/Wifi"
import CellBase from "./CellBase";
import {
  getMemoizeMotionPos,
  getMemoizeMotionState,
  getMemoizeSlaveAlState,
  getMemoizeSlaveOnlineState,
  getCurrentMotionVelPercent
} from "../../selectors";

class JogJoint extends CellBase {
  renderJogJoint = ({getPrefixCls}) => {
    const {
      prefixCls: customizePrefixCls,
      cell,
      slaveOnlineState,
      slaveAlState,
      motionState,
      motionPos,
      motionVelPercent,
    } = this.props
    const prefixCls = getPrefixCls('jog-joint', customizePrefixCls);

    return <div
      className={`${prefixCls}`}
    >
      {
        [0, 1, 2, 3, 4, 5].map((i) => <Row key={i}>
          <Button
            type="primary"
            size={"large"}
            onClick={()=> {
              this.sendCmd(`j${i+1} --direction=-1 --vel_percent=${motionVelPercent}`, cell.i)
            }}
            onMouseDown={()=>{
              this.getOnMouseDownFn(
                `j${i+1} --direction=-1 --vel_percent=${motionVelPercent}`,
                cell.i
              )()
            }}
            onMouseUp={()=>{
              this.getOnMouseUpFn(`j${i+1} --stop`, cell.i)()
            }}
            onMouseOut={()=>{
              this.getOnMouseUpFn(`j${i+1} --stop`, cell.i)()
            }}
          >J{i+1}-</Button>
          <div className={`${prefixCls}-status`}>
            <span>
              {(motionPos && (motionPos[i] || motionPos[i] === 0))?(motionPos[i]/Math.PI*180).toFixed(3):'null'}
            </span>
            <div className={`${prefixCls}-status-icons`}>
              <Icon component={Wifi} className={`${
                (!slaveOnlineState || !slaveOnlineState[i])?`${prefixCls}-slave-online-state disabled`:`${prefixCls}-slave-online-state ${prefixCls}-slave-online-state-${slaveAlState && slaveAlState[i]}`
              }`}/>
              {
                !!(motionState && motionState[i]) && <Icon type="check-circle" theme={"filled"} className={"success"} />
              }
              {
                !!(!motionState || !motionState[i]) && <Icon type="check-circle" theme={"filled"} className={"disabled"}/>
              }
            </div>
          </div>
          <Button
            type="primary"
            size={"large"}
            onClick={()=> {
            this.sendCmd(`j${i+1} --direction=1 --vel_percent=${motionVelPercent}`, cell.i)
          }}
            onMouseDown={()=>{
              this.getOnMouseDownFn(
                `j${i+1} --direction=1 --vel_percent=${motionVelPercent}`,
                cell.i
              )()
            }}
            onMouseUp={()=>{
              this.getOnMouseUpFn(`j${i+1} --stop`, cell.i)()
            }}
            onMouseOut={()=>{
              this.getOnMouseUpFn(`j${i+1} --stop`, cell.i)()
            }}
          >J{i+1}+</Button>
        </Row>)
      }
    </div>
  }

  render(){
    return <ConfigConsumer>{this.renderJogJoint}</ConfigConsumer>
  }
}

JogJoint.propTypes = {
  cell: PropTypes.object.isRequired,
  sendCmd: PropTypes.func.isRequired,
  vel: PropTypes.number,
  motionPos: PropTypes.array,
  slaveOnlineState: PropTypes.array,
}

const getJogJointState = createSelector(
  getCurrentMotionVelPercent,
  getMemoizeMotionPos,
  getMemoizeMotionState,
  getMemoizeSlaveOnlineState,
  getMemoizeSlaveAlState,
  (motionVelPercent, motionPos, motionState, slaveOnlineState, slaveAlState) => {
    return {
      motionVelPercent,
      motionPos,
      motionState,
      slaveOnlineState,
      slaveAlState
    }
  }
)

function mapStateToProps(state) {
  return getJogJointState(state)
}

const ConnectedJogJoint = connect(
  mapStateToProps,
  {
    sendCmd,
  },
)(JogJoint)

ConnectedJogJoint.NAME = 'Jog'

export default ConnectedJogJoint

