import React from 'react'
import {ConfigConsumer} from 'antd/lib/config-provider'
import PropTypes from "prop-types";
import Row from "antd/lib/row"
import Button from "antd/lib/button"
import { createSelector } from 'reselect'
import { connect } from 'react-redux'
import {
  sendCmd
} from '../../../actions'
import CellBase from "../CellBase";
import {
  getMemoizeEndPE,
  getCurrentMotionVelPercent,
  getCurrentCoordinateId, getCurrentToolId, getCurrentWorkObjectId
} from "../../../selectors";


class JogCoordinate extends CellBase {
  renderJogCoordinate = ({getPrefixCls}) => {
    const {
      prefixCls: customizePrefixCls,
      endPE,
      motionVelPercent,
      coordinateId,
      toolId,
      workObjectId,
      cell,
    } = this.props
    const prefixCls = getPrefixCls('jog-coordinate', customizePrefixCls);
    const radioStyle = {
      display: 'block',
      height: '30px',
      lineHeight: '30px',
    };
    const names = ['X', 'Y', 'Z', 'RX', 'RY', 'RZ']
    const types = ['x', 'y', 'z', 'rx', 'ry', 'rz']
    return <div
      className={`${prefixCls}`}
    >
      {
        names.map((name, index)=>(<Row key={name}>
          <Button
            type="primary"
            size={"large"}
            onClick={()=>{
              this.getSendCmdFn(
                `j${types[index]} --vel_percent=${motionVelPercent} --direction=-1 --cor=${coordinateId} --tool=tool${toolId} --wobj=wobj${workObjectId}`,
                cell.i
              )()
            }}
            onMouseDown={()=>{
              this.getOnMouseDownFn(
                `j${types[index]} --vel_percent=${motionVelPercent} --direction=-1 --cor=${coordinateId} --tool=tool${toolId} --wobj=wobj${workObjectId}`,
                cell.i
              )()
            }}
            onMouseUp={()=>{
              this.getOnMouseUpFn(`j${types[index]} --stop`, cell.i)()
            }}
            onMouseOut={()=>{
              this.getOnMouseUpFn(`j${types[index]} --stop`, cell.i)()
            }}
          >{name}-</Button>
          {/* <Row className={`${prefixCls}-end_pe`}> */}
          {/* <div> */}
          {/* {<span>{names[index]}</span>} */}
          <span style={{display: 'inline-block',
      width: '60px',opacity: 0.75,}}>
          {!!(names[index] === 'X') && <span>{!!endPE && (!!endPE[index] || endPE[index]===0)?(endPE[index]*1000).toFixed(3):'null'}</span>}
          {!!(names[index] === 'Y') && <span>{!!endPE && (!!endPE[index] || endPE[index]===0)?(endPE[index]*1000).toFixed(3):'null'}</span>}
          {!!(names[index] === 'Z') && <span>{!!endPE && (!!endPE[index] || endPE[index]===0)?(endPE[index]*1000).toFixed(3):'null'}</span>}
          {!!(names[index] === 'RX') && <span>{!!endPE && (!!endPE[index] || endPE[index]===0)?(endPE[index]/Math.PI*180).toFixed(3):'null'}</span>}
          {!!(names[index] === 'RY') && <span>{!!endPE && (!!endPE[index] || endPE[index]===0)?(endPE[index]/Math.PI*180).toFixed(3):'null'}</span>}
          {!!(names[index] === 'RZ') && <span>{!!endPE && (!!endPE[index] || endPE[index]===0)?(endPE[index]/Math.PI*180).toFixed(3):'null'}</span>}
          </span>
        {/* </div> */}
        {/* </Row> */}
            <Button
              type="primary"
              size={"large"}
              onClick={()=>{
                this.getSendCmdFn(
                  `j${types[index]} --vel_percent=${motionVelPercent} --direction=1 --cor=${coordinateId} --tool=tool${toolId} --wobj=wobj${workObjectId}`,
                  cell.i
                )()
              }}
              onMouseDown={()=>{
                this.getOnMouseDownFn(
                  `j${types[index]} --vel_percent=${motionVelPercent} --direction=1 --cor=${coordinateId} --tool=tool${toolId} --wobj=wobj${workObjectId}`,
                  cell.i
                )()
              }}
              onMouseUp={()=>{
                this.getOnMouseUpFn(`j${types[index]} --stop`, cell.i)()
              }}
              onMouseOut={()=>{
                this.getOnMouseUpFn(`j${types[index]} --stop`, cell.i)()
              }}
            >{name}+</Button>
          </Row>)
        )
      }
    </div>
  }

  render(){
    return <ConfigConsumer>{this.renderJogCoordinate}</ConfigConsumer>
  }
}

JogCoordinate.propTypes = {
  cell: PropTypes.object.isRequired,
  sendCmd: PropTypes.func.isRequired,
  motionVelPercent: PropTypes.number,
  coordinateId: PropTypes.number.isRequired,
  toolId: PropTypes.number.isRequired,
  workObjectId: PropTypes.number.isRequired,
  endPE: PropTypes.array,
}


export const getJogCoordinateState = createSelector(
  getCurrentMotionVelPercent,
  getCurrentCoordinateId,
  getCurrentToolId,
  getCurrentWorkObjectId,
  getMemoizeEndPE,
  (motionVelPercent, coordinateId, toolId, workObjectId, endPE) => {
    return {
      coordinateId,
      motionVelPercent,
      toolId,
      workObjectId,
      endPE
    }
  }
)


function mapStateToProps(state) {
  return getJogCoordinateState(state)
}

const ConnectedJogCoordinate = connect(
  mapStateToProps,
  {
    sendCmd,
  },
)(JogCoordinate)

ConnectedJogCoordinate.NAME = 'Jog'

export default  ConnectedJogCoordinate
