import React from 'react'
import { render } from 'react-dom'
import * as serviceWorker from './serviceWorker';

import { Router, Route } from 'react-router'
import { Provider } from 'react-redux'
import App from './containers/App'
import configureStore from './store'
import rootSaga from './sagas'
import {history} from "./services";
import ConfigProvider from 'antd/lib/config-provider'
//import './style/index.less'
import {Select} from 'antd'
import blue from './style/index_blue.less'
import dark from './style/index.less'
// import PcSkinlOne from '../static/css/skin/pc-skin-1/index.less'
// import PcSkinlTwo from '../static/css/skin/pc-skin-2/index.less'
// import PcSkinlThree from '../static/css/skin/pc-skin-3/index.less'


const {Option}  = Select

const store = configureStore()
store.runSaga(rootSaga)

// this.handleChangeSkin = (value) => {
//   this.props.setSkin(value)
// }

const selectSkin = (type) => {
  switch (type) {
      case 'one':
          return blue
          break;
      case 'two':
          return dark
          break;
  }
}

render(
  <Provider store={store}>
    <ConfigProvider prefixCls={'rchmi'}>
    <Router history={history}>
      <Route  path="/" component={App}/>
    </Router>
    </ConfigProvider>
  </Provider>,
  document.getElementById('root')
)

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();


